.speech-synthesis-reader {
    overflow: auto;
}
.voice.active {
    background-color: cadetblue;
}

.textarea-container {
    margin: 10px;
}

textarea {
    width: 100%;
    height: 150px;
    padding: 12px 20px;
    box-sizing: border-box;
    border: 2px solid #ccc;
    border-radius: 4px;
    background-color: #f8f8f8;
    font-size: 16px;
    resize: none;
  }
